* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f4f4f4;
  background-image: url("https://res.cloudinary.com/dakq2u8n0/image/upload/a_90/v1726497657/1d7c15897dd6a32cfbe6a7d73f4d7ca7_qql5w1.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  font-family: Markazi Text, serif;
  display: flex;
}

canvas.vanta-canvas {
  width: 100%;
  height: 100%;
  display: block;
  box-shadow: 0 5px 5px #70707080;
}

#vanta-background {
  width: 100%;
  height: 50vh;
  position: relative;
}

.overlay-content {
  color: #222;
  z-index: 3;
  text-align: left;
  margin-left: 20px;
  position: absolute;
  top: 200px;
  left: 50%;
  transform: translateX(-100%);
}

.overlay-content img {
  width: auto;
  height: 5rem;
  animation: 3s ease-in-out infinite fadeInOut;
}

.overlay-content p {
  color: #1b1b1b;
}

header {
  box-sizing: border-box;
  z-index: 1000;
  background-image: url("https://res.cloudinary.com/dakq2u8n0/image/upload/v1726497657/1d7c15897dd6a32cfbe6a7d73f4d7ca7_qql5w1.jpg");
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 4px 8px #0003;
}

.header-content {
  flex: 1;
  justify-content: center;
  display: flex;
}

.logo {
  height: 50px;
}

.menu-icon {
  cursor: pointer;
  margin-right: auto;
  font-size: 30px;
}

.date {
  color: #000;
  text-align: right;
  margin-left: auto;
  font-size: 10px;
}

.menu {
  z-index: 999;
  background-color: #fff;
  width: 100%;
  display: none;
  position: absolute;
  top: 75px;
  left: 0;
  box-shadow: 0 8px 16px #0003;
}

.menu.show-menu {
  display: block;
}

.menu ul {
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.menu ul li:hover {
  background: #4a4a4a;
}

.menu ul li {
  text-align: center;
  width: 100%;
  padding: 0;
}

.menu ul li:hover a {
  color: #fff;
}

.menu ul li a {
  color: #000;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 10px;
  font-size: 18px;
  text-decoration: none;
  display: block;
}

main {
  margin: 4.5rem 0;
}

main.content {
  text-align: center;
  color: #333;
  flex: 1;
  justify-content: center;
  align-items: center;
  display: flex;
}

.section-header {
  text-align: center;
  margin: 3rem 0 1rem;
}

.section-header h2 {
  color: #000;
  font-size: 24px;
}

.section-line {
  background-color: #9e9e9e;
  width: 85%;
  height: 2px;
  margin: .5rem auto;
}

#article-container {
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: auto;
  gap: 20px;
  padding: 1rem 20px;
  display: grid;
}

.article img.article-image {
  border-radius: 8px;
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.article {
  flex-direction: column;
  margin-bottom: 1rem;
  padding: 10px;
  display: flex;
}

.article:hover {
  background-color: #cdcbcb;
  border: 1px solid #ddd;
  border-radius: 15px;
}

.article h2 {
  color: #000;
  margin-bottom: 10px;
  font-size: 15px;
}

.article p {
  color: #555;
  font-size: 12px;
}

.article a {
  text-decoration: none;
}

footer {
  text-align: center;
  color: #bababa;
  background-color: #1f1e1e;
  width: 100%;
  padding: 10px 0 15px;
  font-size: 10px;
  line-height: 1.5;
}

footer img {
  width: 120px;
  margin-top: 10px;
}

.scroll-to-top {
  cursor: pointer;
  z-index: 1000;
  background: none;
  border: none;
  padding: 0;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.scroll-to-top img {
  width: 80px;
  height: auto;
  transition: opacity .3s;
}

.scroll-to-top:hover img {
  opacity: .8;
}

@keyframes fadeInOut {
  0% {
    opacity: .1;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: .1;
  }
}

@media screen and (width >= 600px) and (width <= 900px) {
  .scroll-to-top img {
    width: 100px;
  }

  .logo {
    height: 60px;
  }

  .menu-icon {
    font-size: 40px;
  }

  .menu {
    width: 200px;
    top: 85px;
  }

  .date {
    font-size: 16px;
  }

  main {
    margin: 5rem 0;
  }

  .overlay-content {
    top: 180px;
    left: 50%;
  }

  .overlay-content img {
    height: 10rem;
  }

  .overlay-content p {
    font-size: 20px;
  }

  .section-header h2 {
    font-size: 30px;
  }

  .article h2 {
    font-size: 20px;
  }

  .article p, .article a {
    font-size: 16px;
  }

  footer {
    font-size: 18px;
    line-height: 1.2;
  }

  footer img {
    width: 160px;
  }
}

@media screen and (width >= 901px) {
  .scroll-to-top img {
    width: 90px;
  }

  #article-container {
    grid-template-columns: repeat(4, 1fr);
  }

  .menu-icon {
    display: none;
  }

  .menu {
    text-align: center;
    box-shadow: none;
    background: none;
    justify-content: center;
    align-items: center;
    width: auto;
    display: flex;
    position: relative;
    top: auto;
  }

  .menu ul {
    flex-direction: row;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }

  .menu ul li {
    margin: 0;
    padding: 0 10px;
  }

  .menu ul li:hover {
    background: none;
  }

  .menu ul li a {
    color: #000;
    padding: 0 15px;
    font-size: 18px;
    text-decoration: none;
    transition: color .3s, text-shadow .3s;
    position: relative;
  }

  .menu ul li a:hover {
    color: #9b9b9b;
    text-shadow: 0 0 5px #9b9b9b, 0 0 10px #9b9b9b, 0 0 15px #9b9b9b;
    background: none;
  }

  .menu ul li:hover a {
    color: inherit;
  }

  header {
    flex-direction: row;
    justify-content: space-between;
    display: flex;
  }

  .logo {
    order: 2;
    height: 60px;
    padding: 0 3rem;
  }

  .date {
    order: 3;
    margin: 0;
    padding: 0 3rem;
    font-size: 15px;
  }

  main {
    margin: 5.5rem 0;
  }

  .overlay-content {
    top: 180px;
  }

  .overlay-content img {
    height: 7rem;
  }

  .overlay-content p {
    max-width: 400px;
    font-size: 16px;
  }

  .section-header h2 {
    font-size: 25px;
  }

  .article h2 {
    font-size: 18px;
  }

  .article p {
    font-size: 16px;
  }

  footer {
    font-size: 15px;
    line-height: 1.2;
  }

  footer img {
    width: 120px;
  }
}
